    MainPage.prototype.removeBoard = function () {
        var title = prompt("Usuń kolumnę o nazwie");
        if (title != "") {
            var index = this.boardsData.map(function (x) { return x.title; }).indexOf(title);
            if (index != -1) {
                this.boardsData.splice(index, 1);
                this.saveBoard();
                location.reload();
            }
        }
    };
    MainPage.prototype.saveBoard = function () {
        localStorage.setItem("boardData", JSON.stringify(this.boardsData));
    };
    MainPage.prototype.getBoards = function () {
        var _this = this;
        if (JSON.parse(localStorage.getItem('boardData')) != null) {
            this.boardsData = JSON.parse(localStorage.getItem('boardData'));
            this.boardsData.forEach(function (boardData) {
                _this.boards.push(new board_1.Board(boardData));
            });
        }
    };
